import * as shopifyCart from '@shopify/theme-cart';
import * as currencyHelper from '@shopify/theme-currency';

export class Cart
{
	drawer
	overlay
	headerCart
	closeButton

	constructor () {
		this.init()
		this.initHandlers()
	}

	init = () => {
		this.overlay = document.getElementById('cart-drawer')
		this.drawer = this.overlay.getElementsByClassName('cart')[0]
		this.closeButton = this.drawer.getElementsByClassName('close')[0]
		const header = document.querySelector('header.header')
		this.headerCart = header.getElementsByClassName('cart')[0]
	}

	initHandlers = () => {
		this.headerCart.onclick = event => {
			event.preventDefault()
			this.overlay.classList.add('opened')
		}

		this.overlay.onclick = event => {
			this.overlay.classList.remove('opened')
		}

		this.drawer.onclick = (event) => {
			event.stopPropagation()
		}

		this.closeButton.onclick = event => {
			this.overlay.classList.remove('opened')
		}

		const plusBtns = this.drawer.querySelectorAll('.action.plus')
		for (let btn of plusBtns) {
			btn.onclick = this.addItemFromCartHandler
		}

		const minusBtns = this.drawer.querySelectorAll('.action.minus')
		for (let btn of minusBtns) {
			btn.onclick = this.removeItemFromCartHandler
		}

		const minusBtnsOnPage = document.querySelectorAll('[data-remove-cart-item]')
		for (let btn of minusBtnsOnPage) {
			btn.onclick = this.removeItemFromPageHandler
		}

		const plusBtnsOnPage = document.querySelectorAll('[data-add-cart-item]')
		for (let btn of plusBtnsOnPage) {
			btn.onclick = this.addItemFromPageHandler
		}
	}

	addItemFromCartHandler = event => {
		event.preventDefault()
		const variantId = event.currentTarget.closest('[data-variant-id]').getAttribute('data-variant-id')
		shopifyCart.addItem(variantId, 1).then(
			data => {
				let count = 0
				for (let item of data.items) {
					if (`${item.variant_id}` === variantId) {
						count = item.quantity
						break
					}
				}
				const itemOnPage = document.querySelector(`.collection-list-item[data-variant-id="${variantId}"]`)
				if (itemOnPage) {
					const cartActions = itemOnPage.querySelector('.cart-actions')
					const minusBtn = cartActions.firstElementChild
					const numberDiv = minusBtn.nextElementSibling
					numberDiv.innerHTML = count
					if (count > 0) {
						minusBtn.classList.remove('-disabled')
						numberDiv.classList.remove('-disabled')
						itemOnPage.classList.add('-enabled-actions')
					}
					else {
						minusBtn.classList.add('-disabled')
						numberDiv.classList.add('-disabled')
						itemOnPage.classList.remove('-enabled-actions')
					}
				}

				this.updateCart(data)
			}
		)
	}

	addItemFromPageHandler = event => {
		event.preventDefault()
		const container = event.currentTarget.closest('[data-variant-id]')
		const variantId = container.getAttribute('data-variant-id')

		shopifyCart.addItem(variantId, 1).then(
			data => {
				let count = 0
				for (let item of data.items) {
					if (`${item.variant_id}` === variantId) {
						count = item.quantity
						break
					}
				}

				const cartActions = container.querySelector('.cart-actions')
				const minusBtn = cartActions.firstElementChild
				const numberDiv = minusBtn.nextElementSibling
				numberDiv.innerHTML = count
				if (count > 0) {
					minusBtn.classList.remove('-disabled')
					numberDiv.classList.remove('-disabled')
					container.classList.add('-enabled-actions')
				}
				else {
					minusBtn.classList.add('-disabled')
					numberDiv.classList.add('-disabled')
					container.classList.remove('-enabled-actions')
				}

				this.updateCart(data)
			}
		)
	}

	removeItemFromCartHandler = event => {
		event.preventDefault()
		const variantId = event.currentTarget.closest('[data-variant-id]').getAttribute('data-variant-id')
		const count = parseInt(event.currentTarget.nextElementSibling.innerHTML)

		shopifyCart.changeItem(variantId, count - 1).then(
			data => {
				let count = 0
				for (let item of data.items) {
					if (`${item.variant_id}` === variantId) {
						count = item.quantity
						break
					}
				}
				const itemOnPage = document.querySelector(`.collection-list-item[data-variant-id="${variantId}"]`)
				if (itemOnPage) {
					const cartActions = itemOnPage.querySelector('.cart-actions')
					const minusBtn = cartActions.firstElementChild
					const numberDiv = minusBtn.nextElementSibling
					numberDiv.innerHTML = count
					if (count > 0) {
						minusBtn.classList.remove('-disabled')
						numberDiv.classList.remove('-disabled')
						itemOnPage.classList.add('-enabled-actions')
					}
					else {
						minusBtn.classList.add('-disabled')
						numberDiv.classList.add('-disabled')
						itemOnPage.classList.remove('-enabled-actions')
					}
				}

				this.updateCart(data)
			}
		)
	}

	removeItemFromPageHandler = event => {
		event.preventDefault()
		const container = event.currentTarget.closest('[data-variant-id]')
		const variantId = container.getAttribute('data-variant-id')
		const count = parseInt(event.currentTarget.nextElementSibling.innerHTML)

		if (count > 0) {
			shopifyCart.changeItem(variantId, count - 1).then(
				data => {
					let count = 0
					for (let item of data.items) {
						if (`${item.variant_id}` === variantId) {
							count = item.quantity
							break
						}
					}

					const cartActions = container.querySelector('.cart-actions')
					const minusBtn = cartActions.firstElementChild
					const numberDiv = minusBtn.nextElementSibling
					numberDiv.innerHTML = count
					if (count > 0) {
						minusBtn.classList.remove('-disabled')
						numberDiv.classList.remove('-disabled')
						container.classList.add('-enabled-actions')
					}
					else {
						minusBtn.classList.add('-disabled')
						numberDiv.classList.add('-disabled')
						container.classList.remove('-enabled-actions')
					}

					this.updateCart(data)
				}
			)
		}
	}

	updateCart = (data) => {
		const updateCartDrawer = data => {

			console.log(data)

			// Update header cart count
			this.headerCart.innerHTML = `${window.theme.strings.cartHeader} (${data.item_count})`

			// Update drawer cart
			const itemsDom = this.drawer.getElementsByClassName('items-container')[0]
			while (itemsDom.firstChild) {
				itemsDom.removeChild(itemsDom.firstChild)
			}

			for (let item of data.items) {
				const itemDiv = document.createElement('div')
				itemDiv.classList.add('item')
				itemDiv.setAttribute('data-variant-id', item.variant_id)
				const itemWrapper = document.createElement('div')
				itemWrapper.classList.add('item-wrapper')
				const imageWrapper = document.createElement('div')
				imageWrapper.classList.add('image')
				const image = document.createElement('img')
				image.src = item.image.replace('.jpg', '_65x65.jpg')
				imageWrapper.appendChild(image)
				itemWrapper.appendChild(imageWrapper)
				const countDiv = document.createElement('div')
				countDiv.classList.add('count')
				const countWrapper = document.createElement('div')
				countWrapper.classList.add('count-wrapper')
				const link = document.createElement('a')
				link.href = item.url
				link.innerHTML = item.product_title
				link.classList.add('title')
				countWrapper.appendChild(link)
				const updatesInput = document.createElement('input')
				updatesInput.type = 'number'
				updatesInput.setAttribute('name', 'updates[]')
				updatesInput.id = `updates_${item.key}`
				updatesInput.setAttribute('value', `${item.quantity}`)
				updatesInput.setAttribute('min', "0")
				updatesInput.setAttribute('aria-label', window.theme.strings.cartItemQuantity)
				updatesInput.setAttribute('hidden', 'hidden')
				countWrapper.appendChild(updatesInput)

				const actions = document.createElement('div')
				actions.classList.add('actions')
				const wrapper = document.createElement('div')
				wrapper.classList.add('actions-wrapper')
				const minusButton = document.createElement('button')
				minusButton.classList.add('action', 'minus')
				minusButton.innerHTML = '-'
				minusButton.onclick = this.removeItemFromCartHandler
				wrapper.appendChild(minusButton)
				const numberDiv = document.createElement('div')
				numberDiv.classList.add('number')
				numberDiv.innerHTML = item.quantity
				wrapper.appendChild(numberDiv)
				const plusButton = document.createElement('button')
				plusButton.classList.add('action', 'plus')
				plusButton.innerHTML = '+'
				plusButton.onclick = this.addItemFromCartHandler
				wrapper.appendChild(plusButton)
				actions.appendChild(wrapper)
				countWrapper.appendChild(actions)
				countDiv.appendChild(countWrapper)
				itemWrapper.appendChild(countDiv)
				const priceDiv = document.createElement('div')
				priceDiv.classList.add('price')
				priceDiv.innerHTML = `${currencyHelper.formatMoney(item.line_price, window.theme.moneyFormat)}`
				itemWrapper.appendChild(priceDiv)
				itemDiv.appendChild(itemWrapper)
				itemsDom.appendChild(itemDiv)
			}

			const subtotalDiv = this.drawer.querySelector('.subtotal > .value')
			subtotalDiv.innerHTML = currencyHelper.formatMoney(data.total_price, window.theme.moneyFormat)

			const checkoutInput = this.drawer.querySelector('.checkout-wrapper [name=checkout]')
			if (checkoutInput) {
				if (data.original_total_price / 100 < window.theme.minimumOrder) {
					checkoutInput.setAttribute('disabled', 'disabled')
				}
				else {
					checkoutInput.removeAttribute('disabled')
				}
			}
		}

		if (data) {
			updateCartDrawer(data)
		}
		else {
			shopifyCart.getCart().then(data => updateCartDrawer(data))
		}
	}
}

const cart = new Cart();
export default cart;