import '../sections/product';

import $ from 'jquery';
import slick from 'slick-carousel';
import sections from '@shopify/theme-sections';
import * as shopifyCart from '@shopify/theme-cart';
import cart from '../layout/cart';


class ProductTemplate
{
	productSliderSelector
	thumbsContainer
	thumbsItems

	constructor () {
		this.productSliderSelector = '.product-slider'
	}

	addToCart = event => {
		event.preventDefault()
		const variantId = event.currentTarget.previousElementSibling.value
		shopifyCart.addItem(variantId).then(data => cart.updateCart())
	}

	init = () => {
		$(document).ready(() => {
			sections.load('product');

			this.thumbsContainer = document.querySelector('.thumbs');
			this.thumbsItems = this.thumbsContainer.getElementsByClassName('thumbs-item')
			this.initSlider()
			this.initThumbsEvents()
			this.initEvents()
		})
	}

	initEvents = () => {
		document.querySelector('[data-add-to-cart]').onclick = this.addToCart
	}

	initSlider = () => {
		$(this.productSliderSelector).slick({
			accessibility: true,
			lazyLoad: 'ondemand',
			slidesToShow: 1,
			slidesToScroll: 1,
			fade: true,
			arrows: true
		})

		$(this.productSliderSelector).on('beforeChange', (event, slickInstance, currSlide, nextSlide) => {
			for (let item of this.thumbsItems) {
				if (item.getAttribute('data-thumb-index') === `${nextSlide}`) {
					item.classList.add('active')
				}
				else {
					item.classList.remove('active')
				}
			}
		})
	}


	initThumbsEvents = () => {
		const onClickHandler = evt => {
			const currItem = evt.currentTarget
			if (currItem.classList.contains('active')) return

			for (let item of this.thumbsItems) {
				item.classList.remove('active')
			}

			currItem.classList.add('active')
			const index = parseInt(currItem.getAttribute('data-thumb-index'))

			$(this.productSliderSelector).slick('slickGoTo', index)
		}

		for (let item of this.thumbsItems) {
			item.addEventListener('click', onClickHandler)
		}
	}
}

const productTemplate = new ProductTemplate()
export default productTemplate